import { getAdminData } from "actions/admin";
import Loader from "components/Loader";
import React, { useEffect, useState } from "react";

// react-bootstrap components
import {
    Badge,
    Button,
    Card,
    Navbar,
    Nav,
    Table,
    Container,
    Row,
    Col,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import moment from 'moment'
import AppUtils from "utils/AppUtils";

function Charity() {
    const dispatch = useDispatch()
    const admin = useSelector(state => state.admin)
    const data = admin.charity
    const [error, setError] = useState('')

    function getCharityData() {
        dispatch(getAdminData({
            type: 'charity',
            successCb: (response) => {
                AppUtils.showMessage("success", response)
                setError('')
            },
            failureCb: (response) => {
                AppUtils.showMessage("failure", response)
                setError('No charity created yet !!!')
            }
        }))
    }

    useEffect(() => {
        getCharityData()
    }, [])

    function renderCharity() {
        
        return data.map((item,idx)=>{
            let date = moment(item.creationDate).format("MM/DD/YYYY")
            let profile = item.user.profile
            return (
                <tr onClick={()=> window.location.href = `#/admin/donations/${item.id}`} style={{cursor:'pointer'}}>
                    <td >{idx+1}</td>
                    <td >{item.charity}</td>
                    <td >{item.description}</td>
                    <td >{item.website}</td>
                    <td >{item.donation}</td>
                    <td >${item.paid}</td>
                    <td >${item.processed}</td>
                    <td >${item.total}</td>
                    <td >{profile.fullName}</td>
                    <td >{item.donated_by!=''?item.donated_by:'--'}</td>
                </tr>
             )
        })
        
    }
    return (
        <>
            <Container fluid>
                <Row>
                    <Col md="12">
                        <Card className="strpied-tabled-with-hover">
                            <Card.Header>
                                <Card.Title as="h4">Charity List</Card.Title>
                                <p className="card-category">
                                    Here is list of charities
                                 </p>
                            </Card.Header>
                            <Card.Body className="table-full-width table-responsive px-0">
                                {error === ''?(
                                    <Table className="table-hover table-striped">
                                    <thead>
                                        <tr >
                                            <th className="border-0" style={{textAlign:'center !important'}}>ID</th>
                                            <th className="border-0">Title</th>
                                            <th className="border-0">Description</th>
                                            <th className="border-0">Website</th>
                                            <th className="border-0">Link</th>
                                            <th className="border-0">Paid</th>
                                            <th className="border-0">Processed</th>
                                            <th className="border-0">Donations</th>
                                            <th className="border-0">Created By</th>
                                            <th className="border-0">Last Donated</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {renderCharity()}
                                    </tbody>
                                </Table>
                                ):(
                                    <p className="card-category pl-3" >{error}</p>
                                )}
                                
                            </Card.Body>
                        </Card>
                    </Col>

                </Row>
                <Loader active={admin.loading}/>
            </Container>
        </>
    );
}

export default Charity;
