import React, { useEffect, useState } from "react";

// react-bootstrap components
import {
  Badge,
  Button,
  Card,
  Navbar,
  Nav,
  Table,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getAdminData } from 'actions/admin'
import AppUtils from "utils/AppUtils";
import moment from 'moment'
import Loader from "components/Loader";
import { updateLegacy } from "actions/admin";

function Legacy() {

  const dispatch = useDispatch()
  const admin = useSelector(state => state.admin)
  const data = admin.legacy
  const [error, setError] = useState('')

  function getLegacy() {
    dispatch(getAdminData({
      type: 'legacy',
      successCb: (response) => {
        AppUtils.showMessage("success", response)
        setError('')
      },
      failureCb: (response) => {
        AppUtils.showMessage("failure", response)
        setError('No legacy created yet !!!')
      }
    }))
  }

  useEffect(() => {
    getLegacy()
  }, [])

  function gotoDetail(item) {
    // dispatch(updateLegacy(item))
    window.location.href = `#/admin/legacyDetail/${item.id}`
  }
  function renderLegacy() {

    return data.map((item, idx) => {
      let date = moment(item.createdAt).format("MM/DD/YYYY")
      let tribute = item.tribute.length>50?item.tribute.substring(0,70)+"...":item.tribute
      return (
        <tr onClick={()=>gotoDetail(item)} style={{cursor:'pointer'}}>
          <td>{idx + 1}</td>
          <td>{item.name}</td>
          <td>{item.quote}</td>
          <td>{item.headline}</td>
          <td>{tribute}</td>
          <td>{date}</td>
          <td>{item && item.user?item.user.fullName:"--"}</td>
        </tr>
      )
    })
  }
  return (
    <>
      <Container fluid>
        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Card.Title as="h4">Digital Legacy List</Card.Title>
                <p className="card-category">
                  Here is a list of digital legacy
                </p>
              </Card.Header>
              <Card.Body className="table-full-width table-responsive px-0">
                {error === '' ? (
                  <Table className="table-hover table-striped">
                    <thead>
                      <tr>
                        <th className="border-0">ID</th>
                        <th className="border-0">Name</th>
                        <th className="border-0">Quote</th>
                        <th className="border-0">Headline</th>
                        <th className="border-0">Tribute</th>
                        <th className="border-0">Created On</th>
                        <th className="border-0">Created By</th>
                      </tr>
                    </thead>
                    <tbody>
                      {renderLegacy()}
                    </tbody>
                  </Table>
                ) : (
                  <p className="card-category pl-3" >{error}</p>
                )}

              </Card.Body>
            </Card>
          </Col>

        </Row>
        <Loader active={admin.loading} />
      </Container>
    </>
  );
}

export default Legacy;
