import React, { useEffect, useState } from "react";

// react-bootstrap components
import {
  Badge,
  Button,
  Card,
  Navbar,
  Nav,
  Table,
  Container,
  Row,
  Col,
  Form,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import AppUtils from "utils/AppUtils";
import moment from 'moment'
import Loader from "components/Loader";
import { useLocation } from "react-router";
import { getMemorialData } from "actions/admin";
import { legacy_fields } from "const/variables";

function LegacyDetail() {

  const dispatch = useDispatch()
  const admin = useSelector(state => state.admin)
  const data = admin.memorial
  const location = useLocation()
  const [fields, setFields] = useState(legacy_fields)

  function getLegacy() {
    let pathname = location.pathname
    let id = pathname.substring(pathname.lastIndexOf("/") + 1, pathname.length)
    let data = {
      id
    }
    dispatch(getMemorialData({
      data,
      successCb: (response) => {
        AppUtils.showMessage("success response", response)
      },
      failureCb: (response) => {
        AppUtils.showMessage("failure response", response)
      }
    }))
  }

  useEffect(() => {
    getLegacy()
  }, [])

  function renderFields() {

    return fields.map((item) => {
      if (!data[item.type]) {
        return null
      }
      return (
        <Row className="card-container pb-2 mt-3 pl-3 pr-2 pt-2" noGutters>
          <Col>
            <Row>
              <Col md={6} >
                {/* <div className="memorial-fields">  */}
                <label class="card-category" style={{ fontSize: '1.3em' }}>{item.label}</label>
              </Col>
              <Col md={6} >


              </Col>
            </Row>
          </Col>
          <Col md={12} className="pt-2">
            <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{data[item.type] ? data[item.type] : item.placeholder}</p>
          </Col>
        </Row>
      )
    })
  }

  let birthDate = data && data.personalDetail? data.personalDetail.birthDate:""
  let passingDate = data && data.personalDetail?data.personalDetail.passingDate:""
  let date = ''
  
  if (birthDate != "") {
      console.log("birth date ", birthDate)
      // birthDate = moment(birthDate).format("ll").toUpperCase()
      birthDate = AppUtils.formatDate(birthDate)
      console.log("birth date ", birthDate)
  }
  if (passingDate != "") {
      // passingDate = moment(passingDate).format("ll").toUpperCase()
      passingDate = AppUtils.formatDate(passingDate)
  }
  if (birthDate != "" && passingDate != "") {

      date = birthDate + " - " + passingDate
  } 

  return (
    <>
      <Container fluid>
        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Card.Title as="h4">Digital Legacy Detail</Card.Title>
                <Row className="d-flex flex-row">
                  <Col md={6}>
                    <p className="card-category">
                      Here is a detail of digital legacy
                  </p>
                  </Col>
                  <Col md={6}>
                    <p className="card-category float-sm-right pointer" onClick={() => window.history.back()}>Back to legacy</p>
                  </Col>
                </Row>
              </Card.Header>
              <Card.Body >
                <Row className="mt-3 mb-2" noGutters>

                  <Col md={6} >
                    {/* <div className="memorial-fields">  */}
                    <div className="card-container pb-2 pr-2 pt-2 pl-2 mr-2">
                      <label class="card-category" style={{ fontSize: '1.3em' }}>Title</label>
                      <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{data.name!=''?data.name:'--'}</p>
                    </div>
                  </Col>
                  <Col md={6} >
                    <div className="card-container pb-2 pr-2 pt-2 pl-2 ">
                      <label class="card-category" style={{ fontSize: '1.3em' }}>Quote</label>
                      <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{data.quote!=''?data.quote:'--'}</p>
                    </div>
                  </Col>
                </Row>

                <Row className="mt-3 mb-2" noGutters>

                  <Col md={6} >
                    {/* <div className="memorial-fields">  */}
                    <div className="card-container pb-2 pr-2 pt-2 pl-2 mr-2">
                      <label class="card-category" style={{ fontSize: '1.3em' }}>Date of birth</label>
                      <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{birthDate}</p>
                    </div>
                  </Col>
                  <Col md={6} >
                    <div className="card-container pb-2 pr-2 pt-2 pl-2 ">
                      <label class="card-category" style={{ fontSize: '1.3em' }}>Date of passing</label>
                      <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{passingDate}</p>
                    </div>
                  </Col>
                </Row>

                <Row className="mt-3 mb-2" noGutters>

                  <Col md={6} >
                    {/* <div className="memorial-fields">  */}
                    <div className="card-container pb-2 pr-2 pt-2 pl-2 mr-2">
                      <label class="card-category" style={{ fontSize: '1.3em' }}>Headline</label>
                      <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{data.headline!=''?data.headline:'--'}</p>
                    </div>
                  </Col>
                  <Col md={6} >
                    <div className="card-container pb-2 pr-2 pt-2 pl-2 ">
                      <label class="card-category" style={{ fontSize: '1.3em' }}>Tribute</label>
                      <p class="text card-category memorial-fields" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{data.tribute!=''?data.tribute:'--'}</p>
                    </div>
                  </Col>
                </Row>


                <Row>
                  <Col md="12">
                    {data.bio && (
                      <Row className="card-container pb-2 pl-3 pr-2 pt-2 pb-2" noGutters >
                        <Col md={12} >
                          <p class="font-regular card-category" style={{ fontSize: '1.4em' }}>Biography</p>
                        </Col>
                        <Col md={12} className="pt-2">
                          <p class="text font-light card-category" style={{ fontSize: '1.3em', whiteSpace: 'pre-line' }}>{data.bio ? data.bio : "--"}</p>
                         
                        </Col>
                      </Row>
                    )}
                  </Col>
                </Row>
                <Row>
                  <Col md={12}>
                    {renderFields()}
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </Col>

        </Row>
        <Loader active={admin.loading} />
      </Container>
    </>
  );
}

export default LegacyDetail;
