import { getAdminData } from "actions/admin";
import React, { Profiler, useEffect, useState } from "react";
import moment from 'moment'
// react-bootstrap components
import {
  Badge,
  Button,
  Card,
  Navbar,
  Nav,
  Table,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import AppUtils from "utils/AppUtils";
import Loader from "components/Loader";

function Burials() {

  const dispatch = useDispatch()
  const admin = useSelector(state => state.admin)
  const data = admin.burial
  const [error, setError] = useState('')

  function getBurials() {
    dispatch(getAdminData({
      type: 'burial',
      successCb: (response) => {
        AppUtils.showMessage("success", response)
        setError('')
      },
      failureCb: (response) => {
        AppUtils.showMessage("failure", response)
        setError('No burial created yet !!!')
      }
    }))
  }

  useEffect(() => {
    getBurials()
  }, [])

  function renderItems() {

    return data.map((item, idx) => {
      let date = moment(item.createdAt).format("MM/DD/YYYY")
      let tribute = item.tribute.length > 50 ? item.tribute.substring(0, 70) + "..." : item.tribute
      return (
        <tr>
          <td>{idx + 1}</td>
          <td>{item.name}</td>
          <td>{item.quote}</td>
          <td>{item.headline}</td>
          <td>{tribute}</td>
          <td>{date}</td>
        </tr>
      )
    })
  }

  return (
    <>
      <Container fluid>
        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Card.Title as="h4">Burials list</Card.Title>
                <p className="card-category">
                  Here is list of burials
                </p>
              </Card.Header>
              <Card.Body className="table-full-width table-responsive px-0">
                {error === '' ? (
                  <Table className="table-hover table-striped">
                    <thead>
                      <tr>
                        <th className="border-0">ID</th>
                        <th className="border-0">Name</th>
                        <th className="border-0">Quote</th>
                        <th className="border-0">Headline</th>
                        <th className="border-0">Tribute</th>
                        <th className="border-0">Created On</th>
                      </tr>
                    </thead>
                    <tbody>
                      {renderItems()}
                    </tbody>
                  </Table>
                ) : (
                  <p className="card-category pl-3" >{error}</p>
                )}

              </Card.Body>
            </Card>
          </Col>

        </Row>
        <Loader active={admin.loading} />
      </Container>
    </>
  );
}

export default Burials;
