import React, { useEffect, useState } from "react";
// react-bootstrap components
import {
  Card,
  Table,
  Container,
  Row,
  Col,
  Image
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getAdminData } from 'actions/admin'
import AppUtils from "utils/AppUtils";
import moment from 'moment'
import profileImg from "assets/img/faces/face-0.jpg"
import Loader from "components/Loader";


function Users() {


  const dispatch = useDispatch()
  const admin = useSelector(state => state.admin)
  const data = admin.users

  const [error, setError] = useState('')

  function getUsers() {
    dispatch(getAdminData({
      type: 'user',
      successCb: (response) => {
        AppUtils.showMessage("success", response)
        setError('')
      },
      failureCb: (response) => {
        AppUtils.showMessage("failure", response)
        setError("No users are registered yet !!!")
      }
    }))
  }
  
  useEffect(() => {
    getUsers()
 
  }, [])

  function renderUsers() {

    return data.map((item, idx) => {
      let date = moment(item.creationDate).format("MM/DD/YYYY")
      let profile = item.avatar && item.avatar != '' ? item.avatar : profileImg
      if (!item.email) {
        return
      }
      return (
        <tr className="pointer" onClick={()=>gotoDetail(item)}>
          <td>{idx + 1}</td>
          <td><Image src={profile} className={"profile-img"} /></td>
          <td>{item.profile.firstName + " " + item.profile.lastName}</td>
          <td>{item.email}</td>
          <td>{item.os === '' ? '--' : item.os}</td>
          {/* <td>{item.providerId}</td> */}
          <td>{date}</td>
        </tr>
      )
    })
  }

  function gotoDetail(item) {
    // dispatch(updateLegacy(item))
    window.location.href = `#/admin/userDetail/${item.id}`
  }


  return (
    <>
      <Container fluid>
       

        <Row>
          <Col md="12">
            <Card className="strpied-tabled-with-hover">
              <Card.Header>
                <Card.Title as="h4">Registered Users</Card.Title>
                <p className="card-category">
                  Here is list of users
                </p>
              </Card.Header>
              <Card.Body className="table-full-width table-responsive px-0">
                {error === '' ? (
                  <Table className="table-hover table-striped">
                    <thead>
                      <tr>
                        <th className="border-0">ID</th>
                        <th className="border-0">Image</th>
                        <th className="border-0">Name</th>
                        <th className="border-0">Email</th>
                        <th className="border-0">OS</th>
                        {/* <th className="border-0">Login Type</th> */}
                        <th className="border-0">Created On</th>
                      </tr>
                    </thead>
                    <tbody>

                      {renderUsers()}
                    </tbody>
                  </Table>
                ) : (
                  <p className="card-category pl-2">{error}</p>
                )}

              </Card.Body>
            </Card>
          </Col>

        </Row>
        <Loader active={admin.loading} />
      </Container>
    </>
  );
}

export default Users;
