import { getAdminData } from "actions/admin";
import Loader from "components/Loader";
import React, { useEffect, useState } from "react";

// react-bootstrap components
import {
    Badge,
    Button,
    Card,
    Navbar,
    Nav,
    Table,
    Container,
    Row,
    Col,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import moment from 'moment'
import AppUtils from "utils/AppUtils";
import { getDonationData } from "actions/requests";
import { sendDonation } from "actions/requests";
import { useToasts } from "react-toast-notifications";

function Requests() {
    const dispatch = useDispatch()
    const request = useSelector(state => state.requests)
    const data = request.requests
    const [error, setError] = useState('')
    const { addToast } = useToasts()

    function getDonation() {
        dispatch(getDonationData({
            
            successCb: (response) => {
                AppUtils.showMessage("success", response)
                setError('')
            },
            failureCb: (response) => {
                AppUtils.showMessage("failure", response)
                setError('No donations requests found yet !!!')
            }
        }))
    }

    function sendDonationData(item) {
        AppUtils.showMessage(item._id)
        
        dispatch(sendDonation({
            request_id:item._id,
            successCb: (response) => {
                AppUtils.showMessage("success", response)
                addToast('Donation credited successfully', {
                    appearance: 'success',
                    autoDismiss: true
                })
                getDonation()
            },
            failureCb: (response) => {
                AppUtils.showMessage("failure", response)
                const errors = response.errors[0]
                addToast(errors, {
                    appearance: 'error',
                    autoDismiss: true,
                })
            }
        }))
    }

    useEffect(() => {
        getDonation()
    }, [])

    function renderDonations() {

        return data.map((item, idx) => {
            let date = moment(item.creationDate).format("MM/DD/YYYY")
            let charity = item.charity ? item.charity.charity : 'No charity'
            return (
                <tr>
                    <td>{idx + 1}</td>
                    <td>{charity}</td>
                    <td>${item.amount}</td>
                    <td>{date}</td>
                    <td>
                        {item.status === 'requested' ? (
                            <div>
                                <Button className="btn-fill" variant="success" onClick={()=>sendDonationData(item)}>Send Money</Button>
                                {/* <Button className="btn-fill ml-2" variant="danger">Decline</Button> */}
                            </div>
                        ) :
                            (
                                <p>{item.status}</p>
                            )}
                    </td>
                </tr>
            )
        })

    }
    return (
        <>
            <Container fluid>
                <Row>
                    <Col md="12">
                        <Card className="strpied-tabled-with-hover">
                            <Card.Header>
                                <Card.Title as="h4">Donations Requests</Card.Title>
                                <p className="card-category">
                                    Here is list of donation requests
                                 </p>
                            </Card.Header>
                            <Card.Body className="table-full-width table-responsive px-0">
                                {error === '' ? (
                                    <Table className="table-hover table-striped">
                                        <thead>
                                            <tr>
                                                <th className="border-0">ID</th>
                                                <th className="border-0">Charity</th>
                                                <th className="border-0">Amount</th>
                                                <th className="border-0">Created At</th>
                                                <th className="border-0">Status</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {renderDonations()}
                                        </tbody>
                                    </Table>
                                ) : (
                                    <p className="card-category pl-3" >{error}</p>
                                )}

                            </Card.Body>
                        </Card>
                    </Col>

                </Row>
                <Loader active={request.loading} />
            </Container>
        </>
    );
}

export default Requests;
