import { getAdminData } from "actions/admin";
import Loader from "components/Loader";
import React, { useEffect, useState } from "react";

// react-bootstrap components
import {
    Badge,
    Button,
    Card,
    Navbar,
    Nav,
    Table,
    Container,
    Row,
    Col,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import moment from 'moment'
import AppUtils from "utils/AppUtils";
import { useLocation } from "react-router";

function Donations() {
    const dispatch = useDispatch()
    const admin = useSelector(state => state.admin)
    const location = useLocation()
    const data = admin.donation
    const [error, setError] = useState('')

    function getDonationData() {
        let pathname = location.pathname
        pathname = pathname.substr(pathname.lastIndexOf("/")+1,pathname.length)
        // AppUtils.showMessage(pathname)
        dispatch(getAdminData({
            type: 'donation',
            charity:pathname,
            successCb: (response) => {
                AppUtils.showMessage("success", response)
                setError('')
            },
            failureCb: (response) => {
                AppUtils.showMessage("failure", response)
                setError('No donations found yet !!!')
            }
        }))
    }

    useEffect(() => {
        getDonationData()
    }, [])

    function renderDonations() {
        
        return data.map((item,idx)=>{
            let date = moment(item.creationDate).format("MM/DD/YYYY")
            let charity = item.charity?item.charity.charity:'No charity'
            return (
                <tr>
                    <td>{idx+1}</td>
                    <td>{item.name}</td>
                    <td>{item.email}</td>
                    <td>${item.amount}</td>
                    <td>{charity}</td>
                    <td>{date}</td>
                </tr>
             )
        })
        
    }
    return (
        <>
            <Container fluid>
                <Row>
                    <Col md="12">
                        <Card className="strpied-tabled-with-hover">
                            <Card.Header>
                                <Card.Title as="h4">Donations List</Card.Title>
                                <p className="card-category">
                                    Here is list of donations
                                 </p>
                            </Card.Header>
                            <Card.Body className="table-full-width table-responsive px-0">
                                {error === ''?(
                                    <Table className="table-hover table-striped">
                                    <thead>
                                        <tr>
                                            <th className="border-0">ID</th>
                                            <th className="border-0">Name</th>
                                            <th className="border-0">Email</th>
                                            <th className="border-0">Amount</th>
                                            <th className="border-0">Charity</th>
                                            <th className="border-0">Created At</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {renderDonations()}
                                    </tbody>
                                </Table>
                                ):(
                                    <p className="card-category pl-3" >{error}</p>
                                )}
                                
                            </Card.Body>
                        </Card>
                    </Col>

                </Row>
                <Loader active={admin.loading}/>
            </Container>
        </>
    );
}

export default Donations;
