import React, { useEffect } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import { Formik } from 'formik'
import logo from "assets/img/logo.png";
import * as Yup from 'yup'
import SemanticInput from 'components/SemanticInput';
import ButtonView from 'components/Button'
import Loader from 'components/Loader'
import AppUtils from 'utils/AppUtils';
import { useDispatch, useSelector } from 'react-redux';
import { loginAccount } from 'actions/auth'
import { useToasts } from 'react-toast-notifications';
function Login() {
    const { addToast } = useToasts()
    const dispatch = useDispatch()
    const auth = useSelector(state => state.auth)
    const validationSchema = Yup.object().shape({

        email: Yup.string()
            .email("Enter  valid email address")
            .max(100, "Email must be less than 100 characters")
            .required("Email is required"),
        password: Yup.string()
            .required("Password is required")
    });

    useEffect(() => {
        checkLogged()
    }, [])

    function checkLogged() {
        let accessToken = localStorage.getItem('admin_access_token')
        if (accessToken) {
            window.location.href = '#admin/dashboard'
            return
        }
        AppUtils.showMessage('access_token', accessToken)

    }

    function loginUser(values) {
        AppUtils.showMessage("values", values)
        //   window.location.href = '#admin/dashboard'
        dispatch(loginAccount({
            email: values.email,
            password: values.password,
            successCb: (response) => {
                AppUtils.showMessage('success response', response)
                window.location.href = '#admin/dashboard'
            },
            failureCb: (response) => {
                AppUtils.showMessage('failure response', response)
                addToast(response.errors[0], {
                    appearance: 'error',
                    autoDismiss: true,
                })
            },
        }))
    }

    return (
        <Container >

            <Row className="mt-5" md={12}>
                <Col md={2} sm={3} />
                <Col md={8} className="mt-5 ml-3 mr-3" sm={6}>
                    <div class="login-container" style={{ padding: "2vw" }}>
                        <Row className="justify-content-center">
                            <img className="img-fluid login-logo" src={logo} />
                        </Row>
                        <Row className="justify-content-center pt-2">
                            <h2 className="text ">Login to admin</h2>
                        </Row>

                        <Formik
                            initialValues={{ email: "", password: "" }}
                            // Hooks up our validationSchema to Formik 

                            validationSchema={validationSchema}
                            enableReinitialize={true}
                            onSubmit={async (values, { setSubmitting, resetForm }) => {
                                console.log(values);

                                loginUser(values)
                            }}>
                            {({
                                values,
                                errors,
                                touched,
                                handleChange,
                                handleBlur,
                                handleSubmit,
                                isSubmitting,
                            }) => (
                                <>
                                    <Row className="justify-content-center pt-3" noGutters>
                                        <Col md={9}>

                                            <SemanticInput
                                                placeholder={'Enter your email address'}
                                                icon={'user'}
                                                position={'left'}
                                                // formStyles={{ marginBottom: 0,width:''}}
                                                name="email"
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.email}
                                                className={touched.email && errors.email ? "error" : null}
                                                onKeyUp={(e) => e.keyCode === 13 ? handleSubmit() : null}
                                            />
                                            {touched.email && errors.email ? (
                                                <div className="error-message">{errors.email}</div>
                                            ) : null}


                                        </Col>
                                        <Col md={9}>
                                            <SemanticInput
                                                type={"password"}
                                                placeholder={'Enter your password'}
                                                formStyles={{ marginTop: "1vh", width: '100%' }}
                                                icon={'lock'}
                                                position={'left'}
                                                name="password"
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.password}
                                                className={touched.password && errors.password ? "error" : null}
                                                onKeyUp={(e) => e.keyCode === 13 ? handleSubmit() : null}
                                            />
                                            {touched.password && errors.password ? (
                                                <div className="error-message">{errors.password}</div>
                                            ) : null}
                                        </Col>
                                    </Row>


                                    <Row className={'pt-3 justify-content-center'} noGutters>
                                        <Col md={9} className="d-flex justify-content-center">
                                            <ButtonView
                                                variant={'success'}
                                                title={'Login'}
                                                style={{ fontSize: '1.2em', width: "98%" }}
                                                block={true}
                                                onClick={handleSubmit}
                                            />
                                        </Col>
                                    </Row>
                                    <Row className={'justify-content-center pt-2'} noGutters>
                                        <Col md={6} lg={8}>
                                            <a href="#/forgot"><p className="text-center font-weight-bold no-margin">Forgot your password?</p></a>
                                        </Col>
                                    </Row>

                                    <Loader
                                        active={auth.loading}
                                    />
                                </>
                            )}
                        </Formik>

                    </div>
                </Col>
                <Col md={2} sm={3} />
            </Row>
        </Container>
    )
}

export default Login