import React, { useEffect, useState } from 'react';
// import logo from './../../assets/images/logo.svg';
import { Container, Row, Col, Form } from 'react-bootstrap'
import InputBox from 'components/InputBox';
import ButtonView from 'components/Button';
import logo from "assets/img/logo.png"

import { Formik } from 'formik';
import * as Yup from 'yup'
import Loader from 'components/Loader';
// import { forgotPasswordRequest } from 'actions/auth';
import { useToasts } from 'react-toast-notifications';
import { useDispatch, useSelector } from 'react-redux';
import { PROFILE_DATA } from 'const/app';
import { resetCodePassword } from 'actions/auth';

function Forgot() {

  const [error, setError] = useState("")
  const { addToast } = useToasts()
  const dispatch = useDispatch()
  const auth = useSelector(state=>state.auth) 

  useEffect(() => {
    let access_token = localStorage.getItem(PROFILE_DATA)
    if (access_token!=null) {
      window.location = "#/dashboard"
    }
  }, [])

  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .email("Enter valid email address")
      .required("Email is required")
  });

  function forgotPassword({ email }) {
    
    dispatch(resetCodePassword({
      email,
      successCb: () => {
        addToast("Email send please check your email.", {
          appearance: 'success',
          autoDismiss: true,
        })
      },
      failureCb: (response) => {
        // setError(response.errors[0])
        addToast(response.errors[0], {
          appearance: 'error',
          autoDismiss: true,
      })
      }
    }))
  }

  return (
    <Container fluid={'md'} className="mb-1">
      {/* <DashboardHeader
        login={false}
      /> */}
    
      <Row className="mt-5" md={12}>
        <Col md={2} />
        <Col md={8} className="mt-5 ml-3 mr-3">
          <Formik
            initialValues={{ email: "" }}
            // Hooks up our validationSchema to Formik 
            validationSchema={validationSchema}
            onSubmit={async (values, { setSubmitting, resetForm }) => {
              console.log(values);
              forgotPassword(values)
              resetForm({})
            }}>
            {({ values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isSubmitting,
            }) => (
                <div class="login-container" style={{ padding: "2vw" }}>
                  <Row className="justify-content-center">
                    <img className="img-fluid login-logo" src={logo} />
                  </Row>
                  <Row className="justify-content-center pt-2">
                    <h2 className="text ">Forgot Password</h2>
                  </Row>
                  <Row className="justify-content-center pt-2">
                    <Col md={8} className="d-flex flex-column">
                      <InputBox
                        placeholder={'Your email Address'}
                        type={'email'}
                        label={''}
                        formStyles={{ width: "100%", marginBottom: 0 }}
                        controlId="email"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.email}
                        className={touched.email && errors.email ? "error" : null}
                      />

                    </Col>
                    <Col md={8} className="pb-2">
                      {touched.email && errors.email ? (
                        <div className="error-message">{errors.email}</div>
                      ) : null}
                      {error != '' ? (
                        <div className="error-message">{error}</div>
                      ) : null}
                    </Col>
                  </Row>


                  <Row className={'pt-3 justify-content-center'} noGutters>
                    <Col md={10} className="d-flex justify-content-center">
                      <ButtonView
                        variant={'success'}
                        title={'Send Reset Password Link'}
                        style={{ fontSize: '1.2em' }}

                        onClick={handleSubmit}
                      />
                    </Col>
                  </Row>

                  <Row className={'justify-content-center pt-2'} noGutters>
                    <Col md={6}>
                      <a href="#/login"><p className="text-center font-weight-bold">Already have password?</p></a>
                    </Col>
                  </Row>
                  <Loader
                    active={auth.loading}
                  />
                </div>
              )}
          </Formik>
        </Col>
        <Col md={2} />
      </Row><br />
    </Container>
  )
}

export default Forgot