    const dispatch = useDispatch()
    const admin = useSelector(state => state.admin)
    const user = admin.user
    const location = useLocation()
    // alert(user.firstName)
    function getUser() {
        let pathname = location.pathname
        let id = pathname.substring(pathname.lastIndexOf("/") + 1, pathname.length)
        // alert(pathname) 
        dispatch(getUserProfile({
            user_id: id,
            successCb: (response) => {
                AppUtils.showMessage("success response", response)
            },
            failureCb: (response) => {
                AppUtils.showMessage("failure response", response)
            }
        }))
    }
    useEffect(() => {
        getUser()
    }, [])
    let gender = user.gender
    let profile = user && user.user && user.user.avatar && user.user.avatar != '' ? user.user.avatar : profileImg
    if (gender != '') {
        switch (gender) {
            case 'm':
                gender = 'Male'
                break
            case 'f':
                gender = 'Female'
                break
            case 'a':
                gender = 'A/T'
                break
        }
    }
    else {
        gender = '--'
    }
