import React, { useEffect, useState } from "react";
// react-bootstrap components
import {
  Card,
  Table,
  Container,
  Row,
  Col,
  Image
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getAdminData } from 'actions/admin'
import AppUtils from "utils/AppUtils";
import moment from 'moment'
import profileImg from "assets/img/faces/face-0.jpg"
import Loader from "components/Loader";
import { getCountData } from "actions/admin";

function Dashboard() {


  const dispatch = useDispatch()
  const admin = useSelector(state => state.admin)
  const data = admin.users
  const count = admin.count
  const [error, setError] = useState('')

  function getUsers() {
    dispatch(getAdminData({
      type: 'user',
      successCb: (response) => {
        AppUtils.showMessage("success", response)
        setError('')
      },
      failureCb: (response) => {
        AppUtils.showMessage("failure", response)
        setError("No users are registered yet !!!")
      }
    }))
  }

  function getAllCount() {
    dispatch(getCountData({
      successCb: (response) => {
        AppUtils.showMessage('success response', response)
      },
      failureCb: (response) => {
        AppUtils.showMessage('failure response', response)
      }
    }))
  }

  useEffect(() => {
    getUsers()
    getAllCount()
  }, [])

  function renderUsers() {

    return data.map((item, idx) => {
      let date = moment(item.creationDate).format("MM/DD/YYYY")
      let profile = item.avatar && item.avatar != '' ? item.avatar : profileImg
      if (!item.email) {
        return
      }
      return (
        <tr className="pointer" onClick={()=>gotoDetail(item)}>
          <td>{idx + 1}</td>
          <td><Image src={profile} className={"profile-img"} /></td>
          <td>{item.profile.firstName + " " + item.profile.lastName}</td>
          <td>{item.email}</td>
          <td>{item.os === '' ? '--' : item.os}</td>
          {/* <td>{item.providerId}</td> */}
          <td>{date}</td>
        </tr>
      )
    })
  }

  function gotoDetail(route) {
    // dispatch(updateLegacy(item))
    window.location.href = `#/admin/${route}`
  }


  return (
    <>
      <Container fluid>
        <Row className="justify-content-end ">
          <Col lg="3" sm="12" md={3} className="pt--2 pb-2">
            <div className="pointer d-flex justify-content-end" onClick={() => getAllCount()} >
              <i className="fas fa-redo mr-1"></i>
                  Refresh Data
                </div>
          </Col>
        </Row>
        <Row>
          <Col lg="3" sm="6">
            <Card className="card-stats pointer" onClick={()=>gotoDetail('users')}>
              <Card.Body>
                <Row>
                  <Col xs="5">
                    <div className="icon-big text-center icon-warning">
                      <i className="nc-icon nc-circle-09 text-success"></i>
                    </div>
                  </Col>
                  <Col xs="7">
                    <div className="numbers">
                      <p className="card-category">Total Users</p>
                      <Card.Title as="h4">{count.users}</Card.Title>
                    </div>
                  </Col>
                </Row>
              </Card.Body>
              {/* <Card.Footer>
                <hr></hr>
                <div className="stats pointer" onClick={()=>getAllCount()} >
                  <i className="fas fa-redo mr-1"></i>
                  Update Now
                </div>
              </Card.Footer> */}
            </Card>
          </Col>
          <Col lg="3" sm="6">
            <Card className="card-stats pointer" onClick={()=>gotoDetail('legacy')}>
              <Card.Body>
                <Row>
                  <Col xs="5">
                    <div className="icon-big text-center icon-warning">
                      <i className="nc-icon nc-notes text-success"></i>
                    </div>
                  </Col>
                  <Col xs="7">
                    <div className="numbers">
                      <p className="card-category">Total Legacies</p>
                      <Card.Title as="h4">{count.legacy}</Card.Title>
                    </div>
                  </Col>
                </Row>
              </Card.Body>
              {/* <Card.Footer>
                <hr></hr>
                <div className="stats pointer" onClick={()=>getAllCount()}>
                <i className="fas fa-redo mr-1"></i>
                  Update now
                </div>
              </Card.Footer> */}
            </Card>
          </Col>
          <Col lg="3" sm="6">
            <Card className="card-stats pointer" onClick={()=>gotoDetail('charity')}>
              <Card.Body>
                <Row>
                  <Col xs="5">
                    <div className="icon-big text-center icon-warning">
                      <i className="nc-icon nc-money-coins text-success"></i>
                    </div>
                  </Col>
                  <Col xs="7">
                    <div className="numbers">
                      <p className="card-category">Total Charities</p>
                      <Card.Title as="h4">{count.charity}</Card.Title>
                    </div>
                  </Col>
                </Row>
              </Card.Body>
              {/* <Card.Footer>
                <hr></hr>
                <div className="stats pointer" onClick={()=>getAllCount()}>
                <i className="fas fa-redo mr-1"></i>
                  Update now
                </div>
              </Card.Footer> */}
            </Card>
          </Col>
          <Col lg="3" sm="6">
            <Card className="card-stats pointer" onClick={()=>gotoDetail('requests')}>
              <Card.Body>
                <Row>
                  <Col xs="5">
                    <div className="icon-big text-center icon-warning">
                      <i className="nc-icon nc-money-coins text-success"></i>
                    </div>
                  </Col>
                  <Col xs="7">
                    <div className="numbers">
                      <p className="card-category">Total Donation Requests</p>
                      <Card.Title as="h4">{count.requests}</Card.Title>
                    </div>
                  </Col>
                </Row>
              </Card.Body>
              {/* <Card.Footer>
                <hr></hr>
                <div className="stats pointer" onClick={()=>getAllCount()}>
                  <i className="fas fa-redo mr-1"></i>
                  Update now
                </div>
              </Card.Footer> */}
            </Card>
          </Col>
        </Row>
        <Loader active={admin.loading} />
      </Container>
    </>
  );
}

export default Dashboard;
